import React from "react";
import styled from "styled-components";
import Todo from "./Todo";

const Wrapper = styled.ul`
  width: 500px;
`;

const Todolist = (props) => {
  return (
    <Wrapper>
      {props.todos.map((e) => {
        return <Todo key={e.id} e={e} />;
      })}
    </Wrapper>
  );
};

export default Todolist;
