import React, { useState } from "react";
import PropTypes from "prop-types";
import styled from "styled-components";

/* ===============================================
#  Style
=============================================== */
const Wrapper = styled.div`
  width: ${(props) => props.width};
  height: ${(props) => props.height};
  background: var(--paleGray);
  border: 1px solid var(--lightGray);
  border-radius: 5px;
  overflow: hidden;
  input {
    width: 100%;
    height: 100%;
    padding: 7px 10px;
  }
  &.focus {
    border: 1px solid var(--gray);
  }
`;

/* ===============================================
#  App
=============================================== */
const Input = (props) => {
  const [focus, toggleFocus] = useState(false);
  const { id, name, type, width, height, className } = props;

  const onChange = (e) => {
    if (props.onChange) {
      props.onChange(e);
    }
  };
  const onFocus = () => {
    if (props.onFocus) {
      props.onFocus();
    }
    toggleFocus(true);
  };
  const onBlur = () => {
    if (props.onBlur) {
      props.onBlur();
    }
    toggleFocus(false);
  };
  return (
    <Wrapper
      width={width}
      height={height}
      className={`${focus ? "focus" : ""} ${className}`}
    >
      <input
        id={id}
        name={name}
        type={type}
        value={props.value}
        onChange={(e) => onChange(e)}
        onFocus={(e) => onFocus(e)}
        onBlur={(e) => onBlur(e)}
        placeholder={props.placeholder}
      />
    </Wrapper>
  );
};

/* ===============================================
#  types
=============================================== */
Input.propTypes = {
  id: PropTypes.string,
  name: PropTypes.string,
  type: PropTypes.string,
  width: PropTypes.string,
  height: PropTypes.string,
  onChange: PropTypes.func,
  onFocus: PropTypes.func,
  onBlur: PropTypes.func,
  className: PropTypes.string,
};
Input.defaultProps = {
  width: "300px",
  height: "35px",
  type: "text",
};

export default Input;
