import React from "react";
import { useStore, useDispatch } from "contexts/contexts.js";
import styled from "styled-components";
import { Button, Checkbox } from "components/libs/Index";
import { toggleTodo, removeTodo } from "actions/actions";

const Wrapper = styled.li`
  list-style: none;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  border-bottom: 1px dashed var(--lightGray);
  padding: 8px 5px;
  .todo_checkbox {
    width: calc(100% - 100px);
    .label {
      cursor: pointer;
      padding-left: 30px;
      text-align: left;
      font-size: 0.95em;
      line-height: 1.5;
      width: 100%;
    }
  }
  &:last-child {
    border-bottom: 0;
  }
  &.done {
    label {
      text-decoration: line-through;
      color: var(--gray);
    }
  }
`;

const Todo = (props) => {
  const state = useStore();
  const dispatch = useDispatch();
  const toggle = toggleTodo();
  const remove = removeTodo();
  const { id, is_done, body } = props.e;

  const handleToggle = (e) => {
    toggle(state, dispatch, id);
  };

  return (
    <Wrapper id={id} key={id} className={is_done ? "done" : ""}>
      <Checkbox
        id={`checkbox_for_${id}`}
        name={id}
        value={id}
        label={body}
        width="13px"
        height="13px"
        checked={is_done ? true : false}
        onChange={handleToggle}
        className="todo_checkbox"
      />
      <Button
        width="85px"
        height="35px"
        onClick={() => remove(state, dispatch, id)}
        transparent
      >
        REMOVE
      </Button>
    </Wrapper>
  );
};

export default Todo;
