import React, { useEffect } from "react";
import styled from "styled-components";
import { useStore, useDispatch } from "contexts/contexts.js";
import { getTodos, postTodo } from "actions/actions";
import { Input, Button } from "components/libs/Index";
import Loading from "components/parts/Loading";
import Todolist from "components/parts/Todolist";

const Wrapper = styled.div`
  h1 {
    font-weight: 600;
    padding: 20px 0;
    font-size: 1.7em;
    span {
      color: var(--lightGray);
      padding-right: 5px;
    }
  }
  form {
    display: flex;
    padding: 20px 0;
  }
  .tabs {
    width: 100%;
    border-bottom: 1px solid var(--lightGray);
    margin-bottom: 10px;
    .tab {
      position: relative;
      &:before {
        content: "";
        width: 0;
        height: 2px;
        background-color: var(--primary);
        position: absolute;
        bottom: -1px;
        left: 0;
        right: 0;
        margin: auto;
        transition: 0.4s ease;
      }
      &.active {
        &:before {
          width: 100%;
        }
      }
    }
  }
`;

const Home = () => {
  const state = useStore();
  const dispatch = useDispatch();
  const post = postTodo();
  const get = getTodos();

  useEffect(() => {
    get(dispatch);
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (state.input !== "" && state.isLoading === false) {
      post(state, dispatch);
    }
  };

  const switchTabs = (str) => {
    dispatch({
      type: "SWITCH_TODOS",
      set: str,
    });
  };

  return (
    <Wrapper>
      <Loading isLoading={state.isLoading} />
      <h1>
        <span>#</span>TODOS
      </h1>
      <form onSubmit={(e) => handleSubmit(e)}>
        <Input
          value={state.input}
          width="calc(100% - 110px)"
          height="40px"
          className="mr-10"
          onChange={(e) =>
            dispatch({
              type: "CHANGE_VALUE",
              data: e.target.value,
            })
          }
          placeholder="TYPE HERE ..."
        />
        <Button type="submit" width="100px">
          + ADD
        </Button>
      </form>
      <div className="flex center tabs">
        <Button
          className={`tab ${state.show === "all" ? "active" : ""}`}
          width="100px"
          onClick={() => switchTabs("all")}
          transparent
        >
          ALL
        </Button>
        <Button
          className={`tab ${state.show === "active" ? "active" : ""}`}
          width="100px"
          onClick={() => switchTabs("active")}
          transparent
        >
          ACTIVE
        </Button>
        <Button
          className={`tab ${state.show === "done" ? "active" : ""}`}
          width="100px"
          onClick={() => switchTabs("done")}
          transparent
        >
          DONE
        </Button>
      </div>
      {state.show === "active" ? <Todolist todos={state.active} /> : ""}
      {state.show === "done" ? <Todolist todos={state.done} /> : ""}
      {state.show === "all" ? <Todolist todos={state.todos} /> : ""}
    </Wrapper>
  );
};

export default Home;
